@import url(https://fonts.googleapis.com/css2?family=Gluten&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
}

body {
  font-family: "Gluten", cursive;
  background: linear-gradient(90deg, #1cb5e0 0%, #000851 100%);
  box-sizing: border-box;
  height: 100vh;
  width: 100vw;
}

.header h1 {
  margin: 10px;
  padding: 10px;
  font-size: 42px;
  color: greenyellow;
}

.search input {
  min-width: 150px;
  min-height: 25px;
  border: none;
  padding: 5px;
  margin: 10px;
  border-radius: 5px;
  font-size: 15px;
}

.main-component {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;
  padding: 10px;
}

.main-card {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.wrapper-card {
  background: skyblue;
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}
.wrapper-card:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  transition: 0.3s;
}

.details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;
  padding: 10px;
}

.details h3,
p {
  margin: 10px;
}

